.Navigation{
  height: 50px;
  background-color: $color-soft-blue;
  width: 375px;
  @include mq("tablet"){
   height: 50px;
   width: 100%;
   background-color: $color-soft-blue;
 }
  .container{
    height: 100%;
    display: flex;
    align-items: center;
    overflow: scroll;
    @include mq("tablet"){
      height: 100%;
      display: flex;
      align-items: center;

    }
  }

  &-item {
    margin-right: 20px;
    font-size: $font-size-small;
    color: $color-navy;
  }
}